@import "varialble";
@import "function";

.container__header {}

.header {
  padding: 25px 0;
  background-color: $bg-clr; }

.header__logo {}

.header__menu {}

.header__menu-item {
  @include fontFSC($goth-m, 14px);
  text-transform: uppercase;
  display: block;
  line-height: 52px;
  padding: 0 9px;
  transition: all 0.2s ease-in-out;
  &:hover {
    color: $orange; } }

.header__menu-item {
  &:last-child {
    padding-right: 0; }

  &:first-child {
    padding-left: 0; } }

.header__menu-item_active {
  color: $orange; }

.header__small-items {}

.header__small-item {
  display: block;
  padding: 0 18px;
  span {
    line-height: 52px;
    @include fontFSC($goth-m, 14px); }
  svg path {
    transition: all 0.2s ease-in-out; } }

.header__small-item {
  &:last-child {
    padding-right: 0; }
  &:first-child {
    padding-left: 0; }
  transition: all 0.2s ease-in-out;
  &:hover {
    span {
      color: $orange; }
    svg path {
      fill: $orange; } } }

.header__arrow-after {
  display: inline-block;
  margin-left: 6px;
  transition: all 0.2s ease-in-out; }

.small-item__img {
  display: block;
  margin-top: 14px;
  position: relative;
  z-index: 5; }

.small-item__cage {
  position: relative;
  &::after {
    content: "";
    display: block;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: #E68336;
    position: absolute;
    top: 12px;
    right: 12px;
    z-index: 1; } }

