@import "sass/varialble";
@import "sass/function";

.card {
  width: 225px;
  height: 440px;
  background: linear-gradient(135deg, #E68336 0%, rgba(230, 131, 54, 0) 100%);
  padding: 1px;
  box-sizing: border-box; }

.card__inner {
  background: $bg-clr;
  padding: 35px 20px 46px;
  box-sizing: border-box; }

.card__heading {
  @include fontFSC($goth-m, 16px);
  line-height: 15px;
  text-align: center; }

.card__img {
  height: 173px; }

.card__price-weight {}

.card__weight {
  @include fontFSC($goth-r, 12px);
  line-height: 16px; }

.card__price-qnt {
  height: 43px;
  border-bottom: 1px solid #fff;
  margin-top: 10px; }

.card__quantity {
  width: 104px;
  height: 34px;
  background: #646161; }

.card__plus {
  @include fontFSC($goth-r, 14px);
  line-height: 34px;
  width: 40px;
  text-align: center;
  cursor: pointer; }

.card__qnt-value {
  @include fontFSC($goth-r, 14px);
  line-height: 34px; }

.card__price {
  @include fontFSC($goth-b, 14px);
  line-height: 35px; }

.card__footer {
  margin-top: 14px; }

.card__min-weight {
  @include fontFSC($goth-r, 12px);
  line-height: 16px;
  width: 123px; }

.card__persons {}

.card__persons-img {
  margin: 0 auto;
  display: block; }

.card__persons-value {
  @include fontFSC($goth-r, 12px); }

.card__button {
  margin: 15px auto 0; }
