@import "sass/varialble";
@import "sass/function";

.container__account {
  margin-top: 45px; }

.account {
  display: flex;
  justify-content: space-between;
  min-height: 650px; }

.account__account-body {}

.account-body {
  width: 728px; }

.account-body__acc-page {}

.acc-page {}

.account__account-sidebar {}

.account-sidebar {
  width: 272px; }

.account-sidebar__item {
  display: block;
  height: 55px;
  padding: 0 0 0 10px;
  box-sizing: border-box;
  line-height: 55px;
  @include fontFSC($goth-r, 16px, $clr);
  transition: all 0.2s ease-in-out;
  &:hover {
    background-color: rgba(46, 46, 46, 0.5); } }


.account-sidebar__item_active {
  background-color: rgba(46, 46, 46, 0.5); }
